import React, {Component} from "react"
import "./styles.scss"
import Details from "../../../content/sole-ber"

export default class SoleberDetails extends Component {
    render() {
        return (
            <div className="soleber-details">
                <div className="soleber-details_heading">Overview</div>
                <div>{Details.overview}</div>

                <div className="soleber-details_row">
                    <img src={require("./imgs/smartSole2.png")} className="soleber-details_imgM"/>
                    <div className="soleber-details_col">
                        <div className="soleber-details_heading">The Challenge</div>
                        <div>{Details.challenge}</div>
                    </div>
                </div>
                <div className="soleber-details_heading">Designing for Users</div>
                <div>{Details.design1}</div>
                <div className="soleber-details_row">
                    <div>{Details.design2}</div>
                </div>
                <div>{Details.design3}</div>
                <div className="soleber-details_row">
                    <img src={require("./imgs/team.png")} className="soleber-details_imgL"/>
                    <div className="soleber-details_col">
                        <div className="soleber-details_heading">The Team</div>
                        <div>{Details.team}</div>
                        <div className="soleber-details_heading">Faculty Advisor</div>
                        <div>{Details.faculty}</div>
                    </div>
                </div>
            </div>
        )
    }
}