import React, {Component} from "react"
import {withRouter} from "react-router-dom"
import "./styles.scss"
import pdf from "../../assets/Resume.pdf"

class NavigationBar extends Component {
    render() {
        return (
            <div className="navigation-bar-container">
                <div className="home-button" onClick={()=>this.props.history.push("/")}>GARION NG</div>
                <div className="navigation-btn-container">
                    <div className="navigation-btn-container_btn" onClick={()=>this.props.history.push({pathname: "/", hash: "work"})}>Work</div>
                    <div  className="navigation-btn-container_btn" onClick={()=>this.props.history.push("/")}>Me</div>
                    <a href={pdf} target="_blank" style={{textDecoration: 'none', color: 'black'}}>
                        <div  className="navigation-btn-container_btn">Resume</div>
                    </a>
                </div>
            </div>
        )
    }
}

export default withRouter(NavigationBar)