import React, {Component} from "react"
import "./styles.scss"

export default class MSDScreen extends Component {
    render() {
        return (
            <div className="msd-container">
                <img className="logo" src={require("./imgs/msgLogo.png")}/>
                <div className="info">
                    <div className="info_primary-name">Professional Attachment</div>
                    <div className="info_primary-description">@ MSD Prague IT Innovation Hub</div>
                    <div className="info_secondary-description">
                        July 2018 - Sept 2018
                    </div>
                    <div className="info_secondary-heading">My Role</div>
                    <div className="info_secondary-description">
                        I served as a Javascript developer intern in a team with three professional developers, working on
                        a new software product for the company. We were developing a React-based web application to be used
                        by clinical researchers for reporting and logging data.
                    </div>
                    <div className="info_secondary-description">
                        Working on a product that was yet to be released, I was able to observe and learn about the activities
                        leading up to the first release. In addition to implementing features from the refined user stories,
                        I also worked closely with UX designers and researchers and observed users interacting with our product.
                    </div>
                    <div className="info_secondary-heading">Skills</div>
                    <div className="info_secondary-description">Node.JS Express, ReactJS, GraphQL, Redux, SCRUM/AGILE programming, and deployment
                        pipelines
                    </div>
                </div>
            </div>
        )
    }
}