import React, {Component} from "react"
import "./styles.scss"

export default class SoleberLanding extends Component {
    render() {
        return (
            <div className="soleber-landing-container">
                <div className="info">
                    <div className="info_primary-name">Sole-ber</div>
                    <div className="info_secondary-description">
                        Aug 2017 - April 2018
                    </div>
                    <div className="info_secondary-heading">Our Goal</div>
                    <div className="info_secondary-description">
                        To create a proof-of-concept prototype of a smart sole that can measure alcohol content found in perspiration and a mobile application that interfaces with the device and displays the data in snippets that are easily digestable.
                    </div>
                    <div className="info_secondary-heading">Skills</div>
                    <div className="info_secondary-description">
                        Android Studios, Java, Arduino,
                        Working with Sensors
                    </div>
                </div>
                <img className="smart-sole-img" src={require("./imgs/smartSole.png")}/>
            </div>
        )
    }
}