import React, {Component} from 'react';
import './App.css';
import HomePage from "./components/homePage"
import NavigationBar from "./components/navigationBar";
import {HashRouter, BrowserRouter, Route, withRouter, Link} from "react-router-dom"
import GlocalScreen from "./components/projectPages/glocal";
import MSDScreen from "./components/projectPages/msd"
import PenguinScreen from "./components/projectPages/penguin";
import ScrollToTop from "./components/scrollToTop"
import SoleberScreen from "./components/projectPages/soleber";

const pages = {
    HOMEPAGE: 'home',
    GLOCAL: 'glocal',
    MERCK: 'merck',
    PENGUIN: 'penguin',
    SIA: 'sia',
    SOLEBER: 'soleber'
}

export default class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            currentScrollHeight: null
        }
    }

    componentDidMount() {
        window.onscroll = () => {
            this.setState({currentScrollHeight: window.scrollY})
        }
    }

    render() {
        const opacity = Math.min(1 - this.state.currentScrollHeight / 500, 1)
        return (
            <HashRouter>
                <ScrollToTop>
                    <div className="App">
                        <Route
                            exact path="/"
                            render={() => <HomePage/>}
                        />
                        <Route
                            exact path="/glocal"
                            render={() => <GlocalScreen/>}
                        />
                        <Route
                            exact path="/msd-professional-attachment"
                            render={() => <MSDScreen/>}
                        />
                        <Route
                            exact path="/smart-penguin-egg"
                            render={() => <PenguinScreen/>}
                        />
                        <Route
                            exact path="/sole-ber"
                            render={() => <SoleberScreen/>}
                        />
                        {(1 - opacity) > 0.2 && <div className="icons-left" style={{opacity: 1 - opacity}}>
                            <a href="https://github.com/noirag" target="_blank"><img
                                src={require("./assets/github.svg")} className="icon"/></a>
                            <a href="https://www.instagram.com/noirag_/" target="_blank"><img
                                src={require("./assets/insta.svg")} className="icon"/></a>
                            <a href="https://www.linkedin.com/in/garion-ng/" target="_blank"><img
                                src={require("./assets/linkedin.svg")} className="icon"/></a>
                        </div>
                        }
                        <div className="vertical-line-left" style={{opacity: 1 - opacity}}/>
                        {(1 - opacity) > 0.2 && <a href="mailto:yuxuanng@hotmail.com" target="_top">
                            <div className="email-right" style={{opacity: 1 - opacity}}>yuxuanng@hotmail.com</div>
                        </a>
                        }
                        <div className="vertical-line-right" style={{opacity: 1 - opacity}}/>
                        <NavigationBar/>
                    </div>
                </ScrollToTop>
            </HashRouter>
        );
    }
}

